<template>
<div class="text-center hero">
    <div><h2>{{ abbreviated(gameData.bees)}} bees</h2>
    <img class="beehive" src="../assets/nest.png" @mousedown="gameData.mousedown = true;" @mouseup="gameData.mousedown=false" @click="clickBeehive()">
    <h4 style="color: red;" :hidden="gameData.isBee">
      need more beehives to make more bee
    </h4>
    <h2>{{ abbreviated(gameData.score) }} honey</h2>
    <h4>{{ gameData.perSecond.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",") }}/s</h4></div>
        <div
        v-for="resource in gameData.resources"
        :key="resource.name"
        class="row"
        style="display: inline-block; width: 10em; padding: 1%"
        >
            <div style="color:tomato">{{ resource.cost.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",") }}</div>
            <button
            class="btn btn-warning"
            style="width:80%"
            :disabled="gameData.score < resource.cost"
            @click="
                resource.count++;
                gameData.score = gameData.score - resource.cost;
                gameData.isBee = true;
                gameData.bees = gameData.bees + resource.rate;
              resource.cost = Math.round(resource.cost * 1.1);
            "
            >
            {{ resource.name }}
            </button>
            <div style="color: yellowgreen;">{{ resource.count }}</div>
            <div style="font-size: 10px;">
            {{ abbreviated((resource.rate / 4) * resource.count) }}/s
            </div>
        </div>
    </div>
</template>

<script>

export default {
    name: 'BeeGame',
    created() {
    this.interval = setInterval(() => this.makeHoney(), 250);
    this.interval = setInterval(() => this.getBees(), this.gameData.holdSpeed);
    },
  //save the game to local storage
    mounted() {
        if (localStorage.gameData) {
            this.gameData = JSON.parse(localStorage.gameData);
            }
    },
    watch: {
        gameData: {
            handler(newgameData) {
                localStorage.gameData = JSON.stringify(newgameData);
            },
        //deep means that the 'watch' gets called even when nested data is updated
            deep: true,
        },
    },
    data() {
        return {
            gameData: {
                resources: {
                  //the buttons are dynamic, so the more resources you add, the bigger the game
                    beehive:    {name: 'beehive',    cost: 500,       rate: 20,     count: 1},
                    better:     {name: 'better hive',cost: 2000,      rate: 100,    count: 0},
                    mansion:    {name: 'mansion',    cost: 10000,     rate: 400,    count: 0},
                    castle:     {name: 'castle',     cost: 60000,     rate: 1600,   count: 0},
                    village:    {name: 'village',    cost: 500000,    rate: 4000,   count: 0},
                    town:       {name: 'town',       cost: 3000000,   rate: 16000,  count: 0},
                    city:       {name: 'city',       cost: 25000000,  rate: 100000, count: 0},
                    beetropolis:{name: 'beetropolis',cost: 300000000, rate: 500000, count: 0},
                    planet:     {name: 'planet',     cost: 1000000000,rate: 1000000,count: 0},
                },
            miles: 0,
            bees: 20,
            perSecond: 0,
            isBee: true,
            score: 0,
            gameOver: false,
            showScore: "",
            mousedown: false,
            holdSpeed: 250,
            imgLeft: null,
            imgTop: null,
            flip: null,
            rotation: null,
            },
        };
    },
    methods: {
        //add bees on click
        getBees() {
            if(this.gameData.isBee){
                if(this.gameData.mousedown){
                    this.gameData.bees++;
                }
            }
        },
        clickBeehive(){
            //WIN THE GAME
            if (this.gameData.resources.planet.count > 7) {
                alert('Bees have taken over the solar system. Congratulations, you win!');
            }
            if(this.gameData.resources.beehive.count <= this.gameData.bees / 100 && this.gameData.resources.better.count < 1){
                this.gameData.isBee = false;
            }
            if(this.gameData.isBee){
                this.gameData.bees++;
            }
        },
        //this runs every 0.25s and controls the bee + number updates
        makeHoney() {
          //calculate the score per second
            this.gameData.score = Math.round(this.gameData.score + this.gameData.perSecond / 4);
            this.gameData.perSecond = this.gameData.bees / 4;
        },
        abbreviated(value){
            var suffixes = ["", "k", "m", "b","t"];
            var suffixNum = Math.floor((""+value).length/3);
            var shortValue = parseFloat((suffixNum != 0 ? (value / Math.pow(1000,suffixNum)) : value).toPrecision(4));
            if(value > 999999){
                if (shortValue % 1 != 0) {
                    shortValue = shortValue.toFixed(3);
                }
                return shortValue+suffixes[suffixNum];
            }else{return value.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}
        }
    }
}


</script>

<style lang="scss" scoped>
.beehive{
    width: 120px;
    transition: transform .2s ease;
}
.beehive:hover{
    transform: scale(1.1);
}
.beehive:active{

    animation: shake 1s;
    animation-iteration-count: infinite;
    transform:scale(1)
}

@keyframes shake {
  0% { transform: translate(1px, 1px) rotate(0deg); }
  10% { transform: translate(-1px, -2px) rotate(-1deg); }
  20% { transform: translate(-3px, 0px) rotate(1deg); }
  30% { transform: translate(3px, 2px) rotate(0deg); }
  40% { transform: translate(1px, -1px) rotate(1deg); }
  50% { transform: translate(-1px, 2px) rotate(-1deg); }
  60% { transform: translate(-3px, 1px) rotate(0deg); }
  70% { transform: translate(3px, 1px) rotate(-1deg); }
  80% { transform: translate(-1px, -1px) rotate(1deg); }
  90% { transform: translate(1px, 2px) rotate(0deg); }
  100% { transform: translate(1px, -2px) rotate(-1deg); }
}


</style>